@charset "UTF-8";
:root {
  --container-width: 1326px;
  --container-padding: 30px;
  --theme-color-accent: #a6a6a6;
  --theme-color-text-common: #121212;
  --font-family: "FuturaNew", sans-serif;
  --second-family: "Jost", sans-serif;
}

/*
Псевдоэлемент-расширитель для кнопок:
1. Помогает попадать по небольшим кнопкам
2. Помогает отлавливать событие нажатия на кнопку при наличии в ней различных элементов
*/
@font-face {
  font-family: FuturaNew;
  src: url("../fonts/FuturaNew-Medium.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: FuturaNew;
  src: url("../fonts/FuturaNew-Heavy.woff2") format("woff2");
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: FuturaNew;
  src: url("../fonts/FuturaNew-ExtraBold.woff2") format("woff2");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: FuturaNew;
  src: url("../fonts/FuturaNew-Demi.woff2") format("woff2");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: FuturaNew;
  src: url("../fonts/FuturaNew-Book.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: FuturaNew;
  src: url("../fonts/FuturaNew-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: Jost;
  src: url("../fonts/Jost-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

input,
button,
textarea {
  padding: 0;
  background: none;
  border: none;
  outline: none;
  color: inherit;
}

html,
body {
  height: 100%;
  padding: 0;
  scroll-behavior: smooth;
  scrollbar-gutter: stable;
}

body {
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

p {
  text-wrap: pretty;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  text-wrap: balance;
}

ul,
ol {
  list-style-type: none;
  padding: 0;
}

a {
  text-decoration: none;
}

.main-wrapper-outer {
  width: 100%;
  min-height: calc(100% - 34px);
  max-width: 460px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;
  background: #f5f5f5;
  border-radius: 40px;
  margin-top: 34px;
  margin-bottom: 34px;
}
@media (max-width: 500px) {
  .main-wrapper-outer {
    border-radius: 0px;
    max-width: 100%;
    min-height: 100%;
    margin-top: 0px;
    margin-bottom: 0px;
  }
}

.main {
  flex-grow: 1;
  flex-shrink: 1;
}

.container {
  padding: 0px 16px;
}

.header {
  position: sticky;
  top: 0;
  left: 0;
  width: 100%;
  padding: 15px 0px;
  border-radius: 40px 40px 0px 0px;
  display: flex;
  justify-content: center;
  box-shadow: 0 4px 20px 0 rgba(125, 125, 125, 0.05);
  background: #fff;
  min-height: 85px;
  transition: opacity 0.3s, border-radius 0.3s;
}
.scroll .header {
  opacity: 0;
  pointer-events: none;
}
.header._header-scroll {
  border-radius: 0px;
}
@media (max-width: 768px) {
  .header {
    min-height: 100%;
    box-shadow: none;
  }
}
@media (max-width: 500px) {
  .header {
    border-radius: 0px;
  }
}
.header__logo {
  display: flex;
  align-items: center;
  justify-content: center;
}
.header__logo img {
  height: 24px;
  width: auto;
}
@media (max-width: 768px) {
  .header__logo img {
    height: 12px;
  }
}
.header__text {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  font-size: 16px;
  line-height: 150%;
  letter-spacing: 0.02em;
  text-align: center;
}

body {
  color: var(--theme-color-text-common);
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 120%;
  background: linear-gradient(224deg, #ffe0e0 0%, #ffdeee 100%);
}
.lock body {
  overflow: hidden;
}
@media (max-width: 500px) {
  body {
    background: transparent;
  }
}

body.noscroll {
  overflow: hidden;
}

input,
button,
textarea {
  font-family: inherit;
  font-weight: 400;
  font-size: 16px;
}

fieldset,
fieldset legend {
  margin: 0;
  border: none;
  padding: 0;
}

button {
  cursor: pointer;
}

a {
  outline: none;
  color: currentcolor;
}

.arrow-back {
  position: sticky;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 48px;
  height: 48px;
  bottom: 43px;
  left: 16px;
  margin: 0px 0px 0px 16px;
  transition: opacity 0.3s;
  border-radius: 50%;
  background: #fff;
  box-shadow: 0 4px 20px 0 rgba(125, 125, 125, 0.05);
}
.arrow-back img {
  width: 24px;
  height: 24px;
}
.arrow-back:hover {
  opacity: 0.7;
}
.scroll .arrow-back {
  opacity: 0;
  pointer-events: none;
}

.block-banner {
  padding: 24px 0px;
}
.block-banner img {
  width: 100%;
  height: auto;
  border-radius: 16px;
}

.block-filter {
  padding: 0px 0px 24px 0px;
}
.block-filter__nav {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin: 0px 0px 24px 0px;
}
@media (max-width: 500px) {
  .block-filter__nav {
    margin: 0px -16px 24px -16px;
    padding: 0px 16px;
    width: calc(100% + 32px);
    flex-wrap: nowrap;
    overflow: auto;
    -ms-overflow-style: none;
    scrollbar-width: none;
  }
  .block-filter__nav::-webkit-scrollbar {
    display: none;
  }
}
.block-filter__title {
  border-radius: 40px;
  padding: 4px 12px;
  background: #fff;
  flex: 0 0 auto;
  font-weight: 500;
  font-size: 14px;
  line-height: 140%;
  color: var(--theme-color-accent);
}
.block-filter__title.active {
  color: var(--theme-color-text-common);
}
.block-filter__body {
  display: grid;
  gap: 16px 11px;
  grid-template-columns: repeat(2, 1fr);
}
.block-filter__body a:hover {
  opacity: 0.7;
}
.block-filter__column {
  display: flex;
  flex-direction: column;
  gap: 10px;
  border-radius: 16px;
  padding: 8px;
  background: #fff;
  transition: opacity 0.3s;
}
.block-filter__column.hide {
  display: none;
}
.block-filter__column.block {
  pointer-events: none;
}
.block-filter__column.block .block-filter__pic1 {
  filter: blur(8px);
}
.block-filter__pic2 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 24px;
  height: 24px;
  z-index: 10;
}
.block-filter__image {
  position: relative;
  border-radius: 12px;
  overflow: hidden;
  height: 190px;
}
.block-filter__pic1 {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.block-filter__item {
  display: flex;
  flex-direction: column;
  gap: 8px;
}
.block-filter__name {
  font-weight: 500;
  font-size: 14px;
  line-height: 140%;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.block-filter__bottom {
  display: grid;
  gap: 10px 16px;
  grid-template-columns: repeat(2, 1fr);
}
.block-filter__icon {
  display: flex;
  align-items: center;
  gap: 4px;
  font-weight: 500;
  font-size: 14px;
  line-height: 140%;
  color: #7a7a7a;
}
.block-filter__icon img {
  width: 16px;
  height: 16px;
}

.block-images {
  display: flex;
  flex-direction: column;
}
.block-images img {
  width: 100%;
  height: auto;
}